import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import Axios from "axios";
import { Box, TextField, Grid } from "@material-ui/core";

export default function Caixa() {
  const products = useState({});

  /* 
  function handleLogout() {
    Axios({
      method: "GET",
      withCredentials: true,
      url: `${process.env.REACT_APP_BACKEND_SERVER}/logout`,
    }).then((res) => {
      setData(null);
      history.push("/login");
    });
  }
  */

  function startVideoAfterSecs() {
    setTimeout(() => {
      var doc = window.document;
      var docEl = doc.documentElement;

      var requestFullScreen = docEl.requestFullscreen || docEl.mozRequestFullScreen || docEl.webkitRequestFullScreen || docEl.msRequestFullscreen;
      var cancelFullScreen = doc.exitFullscreen || doc.mozCancelFullScreen || doc.webkitExitFullscreen || doc.msExitFullscreen;

      if (!doc.fullscreenElement && !doc.mozFullScreenElement && !doc.webkitFullscreenElement && !doc.msFullscreenElement) {
        requestFullScreen.call(docEl);
      } else {
        //cancelFullScreen.call(doc);
      }
    }, 3000);
  }

  useEffect(() => {}, []);

  function getHeight() {
    var body = document.body,
      html = document.documentElement;

    var height = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
    return height;
  }

  return (
    <>
      <Box>
        <Grid container spacing={2} style={{ minHeight: getHeight(), width: "100%", backgroundColor: "yellow" }}>
          <Grid item style={{ height: getHeight(), width: "20%", backgroundColor: "black" }}>
          </Grid>
          <Grid item style={{ width: "80%", marginTop: "1%" }} >
            <TextField id="outlined-basic" style={{ width: "100%" }} /* label="Outlined" */ variant="outlined" />
          </Grid>
        </Grid>
      </Box>
    </>
  );
}
